/* -------------------------------------------------------------------------- */
/*                               Scrollbar style                              */
/* -------------------------------------------------------------------------- */

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, rgba(154,57,163,1) 0%, rgba(65,103,168,1) 100%);
  
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(154,57,163,1);
}